import { Component, OnInit } from '@angular/core';
import { NgConfirmService } from './ng-confirm.service';

@Component({
  selector: 'ng-confirm',
  templateUrl:'./ng-confirm.html' ,
  styleUrls: ['./ng-confirm.scss']
})
export class NgConfirmComponent implements OnInit {

  show: boolean = false;
  message: any;
  constructor(private modalService: NgConfirmService) { }

  ngOnInit(): void {
    this.modalService.displayModa$.subscribe((val) => {
      this.show = val.isVisible;
      this.message = val;
    });
  }
  close() {
    this.modalService.closeConfirm();
  }

}
