import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class NgConfirmService {

  displayModa$ = new Subject<any>();

  constructor() {}

  showConfirm(message: string, yes: () => void, no: () => void) {
    this.displayModa$.next({
      isVisible: true,
      messageText: message,
      yes: () => {
        yes();
        this.closeConfirm();
      },
      no: () => {
        no();
        this.closeConfirm();
      },
    });
  }
  closeConfirm() {
    this.displayModa$.next(false);
  }
}
